














import { VNode } from 'vue'
import { Component, Prop, Vue } from 'vue-property-decorator';
import Digits from './Digits.vue';

@Component({
  components: { Digits },
  props: { value: Number }
})
export default class Fouls extends Vue {
  value!: number;

  get bonus () {
    return this.value >= 5;
  }

  onClick (event: MouseEvent) {
    if (event.ctrlKey) {
      this.$emit('decrement')
    } else {
      if(this.value < 9) {
        this.$emit('increment')
      }
    }
  }
}
